import uuid from 'uuid'

// add an expense object
export const addExpense=({description='',note='', amount=0,
    createdAt=0 }={})=>({
    type: 'ADD_EXPENSE',
    expense:{
        id: uuid(),
        description,
        note,
        amount,
        createdAt
    }
})

// remove expense for object
export const removeExpense= ({id}={})=>(
    {
        type: 'REMOVE_EXPENSE',
        id
    }
)

// edit the expense for object
export const editExpense=(id,updates)=>({
    type: 'EDIT_EXPENSE',
    id,
    updates
})




